<template>
    <div style="height: 600px" ref="ddd">
        <vueCropper ref="cropper"
                    v-if="!isShowCropper"
                    :img="option.img"
                    :outputSize="option.outputSize"
                    :outputType="option.outputType"
                    :info="option.info"
                    :canScale="option.canScale"
                    :autoCrop="option.autoCrop"
                    :autoCropWidth="option.autoCropWidth"
                    :autoCropHeight="option.autoCropHeight"
                    :fixed="option.fixed"
                    :centerBox="option.centerBox"
                    :fixedNumber="option.fixedNumber"
                    style="position: absolute;z-index: 999; width:1000px;height: 500px"
                        ></vueCropper>
        <el-button
                    v-if="!isShowCropper"
                   type="danger"
                    style="position: fixed;z-index: 9999; bottom: 0"
                   @click="onCubeImg"></el-button>
        <!--<img :src="option.img" alt="" width="500px">-->
        <div v-if="isShowCropper" >
            <img :src="option.img" alt="" ref="newimg"  @dblclick="test" width="1000px">
            <el-button
                    type="primary"
                    @click="onResetImg"></el-button>
            <el-button
                    type="primary"
                    @click="fanzhuan"></el-button>
        </div>
    </div>
</template>

<script>
  import { VueCropper }  from 'vue-cropper'
  export default {
    name: '',
    components: {
      VueCropper
    },
    data () {
      return {
        msg: 'Welcome to Your Vue.js App',
        isShowCropper: true,            //
        option: {
          img: 'http://img.zcool.cn/community/01c8e3554473d80000019ae9961675.jpg',                         //
          info: true,                      //
          outputSize: 1,                   // 
          outputType: 'jpeg',              //
          canScale: false,                 // 
          autoCrop: true,                  // 
          //autoCropWidth: '100%',              // 
          //autoCropHeight: '100%',             // 
          fixedBox:true,
          centerBox:true,
          fixed: false,                    //
          fixedNumber: [4, 4]              //
        },
        url:""
      }
    },
    mounted(){
//      console.log(this.option);
    },
    methods:{
      test(){
        this.isShowCropper = !this.isShowCropper;
      },
      onResetImg(){
        this.isShowCropper = false;
      },
      fanzhuan(){
        this.$refs.newimg.style.transform = 'scaleX(-1)';
      },
      // 
      onCubeImg() {
        // cropperbase64 
        console.log(this.$refs.ddd);
        this.$refs.cropper.getCropData(data => {
//          console.log(data);
          this.url = data
          this.option.img = data;
          this.isShowCropper = true

          //
//          this.option.img = ''

          //base64file
          let file = this.convertBase64UrlToBlob(data)
//          file.name = this.fileUpload.name
          console.log(file);
          //
//          this.uploadFile(file).then(res => {
//            this.form.content = res.file_id    //idfromcontent
//          })

        })
      },
      // base64file
      convertBase64UrlToBlob(urlData) {
        let bytes = window.atob(urlData.split(',')[1]);//urlbyte
        //,ascii00
        let ab = new ArrayBuffer(bytes.length);
        let ia = new Uint8Array(ab);
        for (var i = 0; i < bytes.length; i++) {
          ia[i] = bytes.charCodeAt(i);
        }
        return new Blob([ab], { type: 'image/jpeg' });
      },
    }
  }
</script>

<style scoped>

</style>



